import { Component, OnInit } from '@angular/core';
import { Task } from '../model/task';
import {CdkDragDrop, moveItemInArray, transferArrayItem} from '@angular/cdk/drag-drop';


@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.less']
})
export class ListComponent implements OnInit {

  // todo = [
  //   'Get to work',
  //   'Pick up groceries',
  //   'Go home',
  //   'Fall asleep'
  // ];

  todo: Task[] = [];

  // done = [
  //   'Get up',
  //   'Brush teeth',
  //   'Take a shower',
  //   'Check e-mail',
  //   'Walk dog'
  // ];
  done: Task[] = [];

  selectedTask?: Task;
  newTask?: Task;

  constructor(
  ) {  

    this.todo = JSON.parse(localStorage.getItem('tasklist')) as Task[];
    this.done = JSON.parse(localStorage.getItem('donelist')) as Task[];

    //this.todo = this.taskList;

    if(this.todo == null)
      this.todo = [];

      if(this.done == null)
      this.done = [];
   }

  ngOnInit(): void {
  }

  //public taskList: Task [] = [];

  addTask(){
    let newTask = new Task();
    //this.taskList.push(newTask);
    this.todo.push(newTask);
    localStorage.setItem("tasklist", JSON.stringify(this.todo));
  }

  createNewTask(){
    this.newTask = new Task();
  }

  saveNewTask(){
    this.todo.push(this.newTask);
    localStorage.setItem("tasklist", JSON.stringify(this.todo));
    this.newTask = null;
  }

  removeTask(index:number){
    if(index > -1){
      this.todo.splice(index, 1);

      localStorage.setItem("tasklist", JSON.stringify(this.todo));
    }
  }

  saveTask(task:Task){
    // if(index > -1){
    //   this.todo.splice(index, 1);

    //   localStorage.setItem("tasklist", JSON.stringify(this.todo));
    // }
    console.log(task.title+" - "+task.description);
    localStorage.setItem("tasklist", JSON.stringify(this.todo));
  }

  deleteTask(task:Task){
    
    // }
    // console.log(task.title+" - "+task.description);
    //localStorage.setItem("tasklist", JSON.stringify(this.todo));
    // console.log(this.todo.indexOf(task));
    let index = this.todo.indexOf(task);

    if(index > -1){
      this.todo.splice(index, 1);
      localStorage.setItem("tasklist", JSON.stringify(this.todo));
      this.selectedTask = null;
    }
  }

  drop(event: CdkDragDrop<string[]>) {
    if (event.previousContainer === event.container) {
      moveItemInArray(event.container.data, event.previousIndex, event.currentIndex);
      localStorage.setItem("tasklist", JSON.stringify(this.todo));
        localStorage.setItem("donelist", JSON.stringify(this.done));
    } else {
      transferArrayItem(event.previousContainer.data,
                        event.container.data,
                        event.previousIndex,
                        event.currentIndex);
      //console.log(event.container);
      if(event.container.id == "cdk-drop-list-1"){
        console.log('moved to done liste');
        //console.log(event.previousIndex);
        //this.todo.splice(event.previousIndex, 1);
        //localStorage.setItem("tasklist", JSON.stringify(this.todo));
        console.log(this.todo);
        console.log(this.done);
        

      }else{
        console.log('moved to todo list');
      }

      localStorage.setItem("tasklist", JSON.stringify(this.todo));
        localStorage.setItem("donelist", JSON.stringify(this.done));
    }
  }

  
onSelect(task: Task): void {
  console.log('selected item');
  this.selectedTask = task;
}

}
